<template>
    <div class="v-container">
        <el-breadcrumb>
            <el-breadcrumb-item :to="{ path: '/tool' }">工具</el-breadcrumb-item>
            <el-breadcrumb-item>生成器</el-breadcrumb-item>
        </el-breadcrumb>
        <hr>
        <div style="padding-top: 2px;">

            <el-form ref="searchFormRef" :inline="true" :model="searchFormData" size="mini">
                <!--<el-form-item label="姓氏:" prop="first_name">-->
                <!--<el-input v-model="searchFormData.first_name"></el-input>-->
                <!--</el-form-item>-->
                <!--<el-form-item label="名字:" prop="last_name">-->
                <!--<el-input v-model="searchFormData.last_name"></el-input>-->
                <!--</el-form-item>-->
                <el-form-item label="性别" prop="sex">
                    <el-select v-model="searchFormData.sex" placeholder="请选择性别" clearable>
                        <el-option v-for="item in sex_code_list"
                                   :key="item.codeId"
                                   :label="item.codeValue"
                                   :value="item.codeId">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="出生日期:" prop="bron_range">
                    <el-date-picker
                            v-model="searchFormData.bron_range"
                            type="daterange"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="起始号码" prop="start_mobile">
                    <el-input v-model="searchFormData.start_mobile"></el-input>
                </el-form-item>
                <div class="v-center">
                    <el-button size="mini" type="primary" @click="create"><i class="iconfont icon-chaxun"></i> 生成数据</el-button>
                    <el-button size="mini" type="primary" @click="onReset"><i class="iconfont icon-chongzhi"></i> 重置</el-button>
                </div>
            </el-form>
            <hr>
            <el-table :data="tableData" :border="true" :stripe="true" size="mini" :height="getTableHeight(130)">
                <el-table-column fixed type="index" :index="onIndexMethod(searchFormData.pageNum,searchFormData.pageSize)" label="序号" width="60"></el-table-column>
                <el-table-column
                        prop="name"
                        label="姓名"
                        width="80">
                </el-table-column>
                <el-table-column
                        :show-overflow-tooltip="true"
                        prop="cardno"
                        label="身份证号"
                        width="160">
                </el-table-column>
                <el-table-column
                        prop="bron_date"
                        label="出生日期"
                        width="120">
                </el-table-column>
                <el-table-column
                        :formatter="format_sex"
                        prop="sex_code_id"
                        label="性别"
                        width="60">
                </el-table-column>
                <el-table-column
                        prop="age"
                        label="年龄"
                        width="60">
                </el-table-column>
                <el-table-column
                        :show-overflow-tooltip="true"
                        prop="area_name"
                        label="出生地区"
                        width="280">
                </el-table-column>
                <el-table-column
                        prop="area_id"
                        label="地区编码"
                        width="120">
                </el-table-column>
                <el-table-column
                        prop="mobile"
                        label="手机号"
                        width="120">
                </el-table-column>
                <el-table-column
                        prop="email"
                        label="邮箱">
                </el-table-column>
                <el-table-column
                        prop="qq"
                        label="QQ">
                </el-table-column>
                <el-table-column
                        prop="nickname"
                        label="昵称">
                </el-table-column>

            </el-table>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'generator',
        data() {
            return {
                sex_code_list: null,
                mobile_type_code_list: null,
                searchFormData: {
                    pageNum: 1,
                    pageSize: 14,
                    pageCount: 1
                },
                tableData: []
            };
        },
        async mounted() {
            this.sex_code_list = await this.$setCodeList(this.sex_code_list, 'sex');
            this.mobile_type_code_list = await this.$setCodeList(this.mobile_type_code_list, 'mobile_type');
            this.create();
        },
        methods: {
            format_sex(row, column, cellValue) /*格式化性别类型信息*/ {
                return this.$formatCodeValue(row, this.sex_code_list, column.property)
            },
            onReset() // 重置功能
            {
                this.$refs.searchFormRef.resetFields();
                this.create();
            },
            async create() {
                let data = await this.$search(this.searchFormData, 'OAMP_create_virtual_identity');
                this.searchFormData.pageCount = data.pageCount;
                this.tableData = data.data;
            },
        }
    };
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
    .cont {
        background-color: #FFF;
        border-radius: 15px;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
        padding: 15px;
    }

    .cont_search {
        background-color: #CCCCCC;
        border-radius: 15px;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
        padding: 15px;
    }

    .gen_button {
        text-align: center;
        width: 100%;
        margin: 0px;
    }
</style>



